import { mapActions } from 'vuex'
import uniPopup from '../uni-popup/uni-popup'
import { ghId, upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  components: {
    uniPopup
  },
  methods: {
    ...mapActions([
      'allThumbsUp',
      'allCollection'
    ]),
    onUniPopupStateFn (state) {
      if (state) {
        this.$refs.popup.open()
      } else {
        this.$refs.popup.close()
      }
    },
    // 视频点赞
    onLikeFn () {
      const data = {
        id: this.oItem.id,
        type: 0
      }
      this.oItem['is_like'] = this.oItem.is_like === 1 ? 0 : 1
      this.allThumbsUp(data)
    },
    // 视频收藏
    onCollectionFn () {
      const data = {
        id: this.oItem.id,
        type: 0
      }
      this.oItem['is_collection'] = this.oItem.is_collection === 1 ? 0 : 1
      this.allCollection(data)
    },
    // 分享内容
    onShareFn (scene) {
      let path = null
      switch (this.oItem.skipPath) {
        case 'musicDetails':
          const oJson = {
            vid: this.oItem.vid || this.oItem.id,
            keywords: '',
            authorId: '',
            type: '',
            page: 1
          }
          path = `/pages/subject/subjectMusic/musicDetails/index?oJson=${JSON.stringify(oJson)}`
          break
        case 'videoDetails':
          path = `/pages/subject/subjectVideo/videoDetails/index?oJson=${encodeURIComponent(JSON.stringify(this.oItem))}`
          break
      }
      uni.share({
        scene,
        type: 5,
        provider: 'weixin',
        imageUrl: this.oItem.thumb || '',
        title: this.oItem.title || '瓶盖思维',
        miniProgram: {
          path,
          type: 0,
          id: ghId,
          webUrl: 'http://uniapp.dcloud.io'
        }
      })
    },
    // 跳转页面
    onSkipPageFn (url) {
      uni.navigateTo({
        url
      })
    }
  },
  props: {
    oItem: {
      type: Object,
      default: {}
    }
  }
}
